$(".burger").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})

$("._show-login").click(function () {
    $(".auth-form").removeClass("_active")
    $(".auth-form._login").addClass("_active")
})
$("._show-reg").click(function () {
    $(".auth-form").removeClass("_active")
    $(".auth-form._reg").addClass("_active")
})


$(".header-user__btn").click(function () {
    $(".header-user").toggleClass("_show-modal")
})

$(".auth__bg").click(function () {
    $(".header-user").removeClass("_show-modal")
})

$(document).mouseup( function(e){
    let div = $( ".header-user" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_show-modal")){
            $(div).removeClass("_show-modal")
        }
    }
});

