let reqForm = document.querySelector("._req-form")

if(reqForm){
    reqForm.onsubmit = async function(e){
        e.preventDefault()
        let fd = new FormData();
        let name = reqForm.querySelector('[name="user-name"]').value
        let phone = reqForm.querySelector('[name="user-phone"]').value
        let email = reqForm.querySelector('[name="user-email"]').value

        fd.append('phone', phone);
        fd.append('name', name);
        fd.append('email', email);


        console.log(name)
        console.log(phone)
        console.log(email)

        try {
            const authResponse = await fetch('https://ideav.online/integram/report/299?JSON&secret=guest', {
                method: 'POST',
                body: fd
            });

            if (!authResponse.ok) {
                throw new Error('Ошибка ответа');
            }
            const result = await authResponse.json();

            openModal("thankModal")
        } catch (error) {
            console.error('Ошибка запроса:', error);
        }
    }
}



